import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { WorkshopDetailComponent } from './workshop-detail.component';
import { WorkshopListComponent } from './workshop-list.component';

const routes: Routes = [
  { path: '', component: WorkshopListComponent },
  { path: 'workshop-create', component: WorkshopDetailComponent },
  { path: 'workshop-detail', component: WorkshopDetailComponent }];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class WorkshopRoutingModule { }
