import { Component, OnInit, ViewChild } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';
import { Router } from "@angular/router";
import { Configuration } from 'src/app/app.configuration';
import { DataService } from 'src/app/shared/services/data-service';
import { NgxSpinnerService } from "ngx-spinner";
import { ToastrService } from 'ngx-toastr';
import { StorageConfiguration } from 'src/app/shared/storage-setting/storage-configuration';
import { MatSort } from '@angular/material/sort';
import { MatDialog } from '@angular/material/dialog';
import { Http } from '@angular/http';
import { ConfirmDialogComponent } from 'src/app/shared/confirm-dialog/confirm-dialog.component';

@Component({
  selector: 'app-workshop-list',
  templateUrl: './workshop-list.component.html',
  styleUrls: ['./workshop-list.component.scss']
})
export class WorkshopListComponent implements OnInit {

  Id: any;
  workshopList = [];
  showAdminContent: boolean = true;
  showManagerContent: boolean = true;
  totalRecords: any;
  SearchValue: any;
  isActive: boolean;
  roleName: any;
  displayedColumns: string[] = ['position', 'workshopName', 'workshopDate', 'dueDate', 'createdDate', 'conductedBy', 'createdBy', 'actions'];
  @ViewChild(MatSort, { static: true }) sorting: MatSort;
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  pageSizeOptions: number[] = [5, 10, 25, 100];
  pageNumber = 1;
  confirmDialogRef: any;
  workshopSearchValue: any;
  id: any;
  path: string;
  constructor(private router: Router,
    private configuration: Configuration,
    private dataService: DataService,
    private storageConfiguration: StorageConfiguration,
    private toastr: ToastrService,
    private spinner: NgxSpinnerService,
    private _matDialog: MatDialog
  ) { }

  ngOnInit() {
    this.showAdminContent = true;
    this.showManagerContent = false;
    this.roleName = this.storageConfiguration.sessionGetItem(this.storageConfiguration.role)
    this.id = this.storageConfiguration.sessionGetItem(this.storageConfiguration.id)
    if (this.roleName == 'staff') {
      this.path = 'sortBy=createdDate&orderBy=desc&createdBy=' + this.id
    }
    else {
      this.path = 'sortBy=createdDate&orderBy=desc'
    }
    this.getWorkshopList()
  }

  viewAsAdmin(isAdmin: boolean) {
    if (isAdmin) {
      this.showAdminContent = true;
      this.showManagerContent = false;
    } else {
      this.showAdminContent = false;
      this.showManagerContent = true;
    }
  }

  getWorkshopList() {
    this.spinner.show();
    this.workshopList = [];
    this.dataService.getAll(this.configuration.workshop + '?' + this.path).subscribe((data: any) => {
      let workshopList = data.data;
      this.totalRecords = data.totalRecords;
      this.workshopList = this.workshopListTableData(workshopList);
      this.spinner.hide();
    }, err => {
      this.spinner.hide();
    });
  }

  async searchWorkshop() {
    if (this.workshopSearchValue != undefined && this.workshopSearchValue != "") {
      await this.dataService.getAll(this.configuration.workshop + '?name=' + this.workshopSearchValue + '&' + this.path).subscribe((data: any) => {
        let workshopList = data.data;
        this.totalRecords = data.data.length;
        this.pageNumber = 1;
        this.paginator.pageIndex = 0;
        this.workshopList = this.workshopListTableData(workshopList);
      }, err => {
        this.spinner.hide();
      });
    } else {
      this.getWorkshopList()
    }
  }

  workshopListTableData(workData) {
    let workListData = [];
    workData.forEach((element, index) => {
      workListData.push({
        workshopId: element._id,
        position: (index + 1) + (this.pageNumber - 1) * 10,
        workshopName: element.name,
        workshopDate: element.workshopDate,
        dueDate: element.dueDate,
        createdDate: element.createdDate,
        conductedByName: element.conductedBy ? element.conductedBy.firstName + ' ' + element.conductedBy.lastName : '',
        conductedByImage: element.conductedBy ? element.conductedBy.profileImageURL : '',
        createdByName: element.createdBy ? element.createdBy.fullName : '',
        createdByImage: element.createdBy ? element.createdBy.profileImageURL : '',
      });
    });
    return workListData;
  }

  nextPage(event) {
    this.spinner.show();
    this.pageNumber = event.pageIndex + 1;
    this.dataService.getAll(this.configuration.workshop + '?pageNumber=' + this.pageNumber + '&' + this.path).subscribe((data: any) => {
      let workshopList = data.data;
      this.totalRecords = data.totalRecords;
      this.workshopList = this.workshopListTableData(workshopList);
      this.spinner.hide();
    }, err => {
      this.spinner.hide();
    });
  }

  applyFilter(event) {

  }

  deleteWorkshop(workshopId) {
    let message = {
      message: 'Do you want to delete this workshop?'
    }
    this.confirmDialogRef = this._matDialog.open(ConfirmDialogComponent, {
      panelClass: 'confirm',
      disableClose: false,
      data: message
    });
    // this.confirmDialogRef.componentInstance.confirmMessage = 'Do you want to delete this workshop?';
    this.confirmDialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.dataService.delete(workshopId, this.configuration.workshop + '/').subscribe(
          (data: any) => {
            this.toastr.success('Workshop deleted successfully!!');
            this.getWorkshopList();
          }, err => {
          }
        );
      }
    });
  }

  editWorkshopDetail(workshopId) {
    this.router.navigate(['/map/workshop/workshop-detail', { workshopId: workshopId }]);
  }

  createWorkshop() {
    this.router.navigate(['/map/workshop/workshop-create']);
  }

}
