import { Component, OnInit, ViewChild, TemplateRef } from '@angular/core';
import { UntypedFormGroup, UntypedFormBuilder, Validators, UntypedFormControl } from '@angular/forms';
import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';
import { MatDialogRef, MatDialog } from '@angular/material/dialog';
import { MatSort } from '@angular/material/sort';
import { Router, ActivatedRoute } from '@angular/router';
import { ConfirmDialogComponent } from 'src/app/shared/confirm-dialog/confirm-dialog.component';
import { StorageConfiguration } from 'src/app/shared/storage-setting/storage-configuration';
import { Configuration } from 'src/app/app.configuration';
import { DataService } from 'src/app/shared/services/data-service';
import { HttpClient } from '@angular/common/http';
import { NgxSpinnerService } from "ngx-spinner";
import { ToastrService } from 'ngx-toastr';
import { AddParticipationComponent } from 'src/app/shared/add-participation/add-participation.component';
import { Observable } from 'rxjs';
import { startWith, map } from 'rxjs/operators';

@Component({
  selector: 'app-workshop-detail',
  templateUrl: './workshop-detail.component.html',
  styleUrls: ['./workshop-detail.component.scss']
})
export class WorkshopDetailComponent implements OnInit {

  participantList = [];
  roleName: string;
  Id: any;
  roleType: any;
  roles = [];
  selectedValue: any;
  isEdit: boolean = false;
  selectedRoleId: any;
  selectedRoleName: any;
  showAdminContent: boolean = true;
  showManagerContent: boolean = true;
  totalRecords: any;
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  pageSizeOptions: number[] = [5, 10, 25, 100];
  pageNumber = 1;
  workshopForm: UntypedFormGroup;
  confirmDialogRef: MatDialogRef<ConfirmDialogComponent>;
  addpopUp: any;
  @ViewChild("formPopUp", { static: false }) formPopUp: TemplateRef<any>;
  workshopId: any;
  addparticipationDialog: MatDialogRef<AddParticipationComponent>;
  displayedColumns: string[] = ['position', 'firstName', 'lastName', 'email', 'mobile', 'address', 'actions'];
  createdDate: any;
  createdBy: any;
  userList = [];
  userSearchValue: any;
  filteredOptions: Observable<any[]>;
  customerFilteredOptions: Observable<any[]>;
  myControl = new UntypedFormControl();
  selectedUserId: any;
  createdByUserImage: any;
  minDate = new Date();
  conductedByUserImage: any;
  isProfileImage: boolean;
  pageSize = 10;

  constructor(private fb: UntypedFormBuilder,
    private storageConfiguration: StorageConfiguration,
    private configuration: Configuration,
    private dataService: DataService,
    private http: HttpClient,
    private router: Router,
    private route: ActivatedRoute,
    private toastr: ToastrService,
    public dialog: MatDialog,
    private spinner: NgxSpinnerService,
    private _matDialog: MatDialog,
  ) {

    this.workshopForm = this.fb.group({
      name: ['', Validators.required],
      workshopDate: ['', Validators.required],
      dueDate: ['', Validators.required],
      conductedBy: [''],
      createdDate: [''],
      createdBy: [''],
    });

  }

  ngOnInit() {
    this.roleName = this.storageConfiguration.sessionGetItem(this.storageConfiguration.name);
    this.getUserList();

    this.route.params.subscribe(params => {
      this.workshopId = params.workshopId;
    });

    this.getRoles();
    this.showAdminContent = true;
    this.showManagerContent = false;

    if (this.workshopId != '' && this.workshopId != undefined) {
      this.isEdit = true;
      this.editworkshopDetails();
      this.getParticipantList();
    }

  }

  createparticipant() {
    if (this.workshopForm.valid) {
      let workshopForm = this.workshopForm.value;
      let parsedDate = new Date(workshopForm.workshopDate);
      let actualUTCWorkshopDate = new Date(
        Date.UTC(parsedDate.getFullYear(), parsedDate.getMonth(), parsedDate.getDate())
      ).toISOString();
      let workshopModel = {
        name: workshopForm.name,
        dueDate: workshopForm.dueDate,
        workshopDate: actualUTCWorkshopDate,
        conductedBy: this.selectedUserId,
        createdBy: this.storageConfiguration.sessionGetItem(this.storageConfiguration.id),
        updatedBy: this.storageConfiguration.sessionGetItem(this.storageConfiguration.id)
      }
      this.spinner.show();
      this.dataService.addUser(this.configuration.workshop, workshopModel).subscribe(
        (data: any) => {
          if (data.isSuccess == true) {
            this.toastr.success('Workshop Created Successfully!!');
            this.workshopId = data.data._id;
            this.router.navigate(['/map/workshop/workshop-detail', { workshopId: data.data._id }]);
            this.editworkshopDetails();
            this.spinner.hide();
          }
        }, err => {
          this.spinner.show();
          if (err.error.data.errmsg == 'Email Already Exists') {
            this.toastr.error('Email Already Exists.');
          }
          this.spinner.hide();
        }
      );
    }
    else {
      for (const i in this.workshopForm.controls) {
        this.workshopForm.controls[i].markAsDirty();
        this.workshopForm.controls[i].updateValueAndValidity();
      }
      this.toastr.error('Please enter the required fields.');
    }
  }

  editworkshopDetails() {
    this.spinner.show();
    this.dataService.getSingle(this.workshopId, this.configuration.workshop + '/').subscribe(
      (data: any) => {
        let workshopInfo = data.data;
        this.workshopForm.patchValue({
          name: workshopInfo.name,
          workshopDate: workshopInfo.workshopDate,
          dueDate: workshopInfo.dueDate,
          conductedBy: workshopInfo.conductedBy ? workshopInfo.conductedBy.firstName + ' ' + workshopInfo.conductedBy.lastName : '',
          createdDate: workshopInfo.createdDate,
          createdBy: workshopInfo.createdBy ? workshopInfo.createdBy.firstName + ' ' + workshopInfo.createdBy.lastName : ''
        });
        this.isProfileImage = true;
        this.conductedByUserImage = workshopInfo.conductedBy ? workshopInfo.conductedBy.profileImageURL : '';
        this.createdByUserImage = workshopInfo.createdBy ? workshopInfo.createdBy.profileImageURL : '';
        this.createdDate = workshopInfo.createdDate;
        this.createdBy = workshopInfo.createdBy ? workshopInfo.createdBy._id : '';
        this.selectedUserId = workshopInfo.conductedBy ? workshopInfo.conductedBy._id : '';
      }, err => {
        this.spinner.hide();
      }
    );
    this.spinner.hide();
  }


  updateworkshop() {
    let workshopForm = this.workshopForm.value;
    let parsedDate = new Date(workshopForm.workshopDate);
    let actualUTCWorkshopDate = new Date(
      Date.UTC(parsedDate.getFullYear(), parsedDate.getMonth(), parsedDate.getDate())
    ).toISOString();
    let workshopModel = {
      name: workshopForm.name,
      dueDate: workshopForm.dueDate,
      workshopDate: actualUTCWorkshopDate,
      conductedBy: this.selectedUserId,
      createdDate: this.createdDate,
      createdBy: this.createdBy,
      updatedBy: this.storageConfiguration.sessionGetItem(this.storageConfiguration.id)
    }
    this.dataService.updateUser(this.workshopId, this.configuration.workshop + '/', workshopModel).subscribe(
      (data: any) => {
        this.toastr.success('Workshop Updated Successfully.');
      }, err => {
        this.toastr.error(err.error.message);
      }
    );
  }

  getParticipantList() {
    this.spinner.show();
    this.dataService.getAll(this.configuration.participant + '?workshop=' + this.workshopId).subscribe((data: any) => {
      let participantList = data.data;
      this.totalRecords = data.totalRecords;
      this.participantList = this.participantListTableData(participantList);
      this.spinner.hide();
    }, err => {
    });
  }

  participantListTableData(workData) {
    let participantListData = [];
    workData.forEach((element, index) => {
      participantListData.push({
        id: element._id,
        position: (index + 1) + (this.pageNumber - 1) * this.pageSize,
        firstName: element.firstName,
        lastName: element.lastName,
        email: element.email,
        address: element.address.addressLine,
        mobile: element.address.mobile
      });
    });
    return participantListData;
  }

  nextPage(event) {
    this.spinner.show();
    this.pageNumber = event.pageIndex + 1;
    this.pageSize = event.pageSize ? event.pageSize : 10;
    this.dataService.getAll(this.configuration.participant + '?workshop=' + this.workshopId + '&pageNumber=' + this.pageNumber + '&pageSize=' + this.pageSize).subscribe(
      (data: any) => {
        let participantList = data.data;
        this.totalRecords = data.totalRecords;
        this.participantList = this.participantListTableData(participantList);
        this.spinner.hide();
      }, err => {
        this.spinner.hide();
      }
    );
  }

  addparticipationPopup() {
    this.addparticipationDialog = this.dialog.open(AddParticipationComponent, {
      disableClose: false,
      data: {
        workshopId: this.workshopId
      }
    });
    this.addparticipationDialog.afterClosed().subscribe(result => {
      if (result) {
        this.getParticipantList();
      }
    });
  }

  editparticipationdetail(participantId) {
    this.addparticipationDialog = this.dialog.open(AddParticipationComponent, {
      disableClose: false,
      data: {
        workshopId: this.workshopId,
        participantId: participantId,
        type: 'edit'
      }
    });
    this.addparticipationDialog.afterClosed().subscribe(result => {
      if (result) {
        this.getParticipantList();
      }
    });
  }


  deleteParticipant(participantId) {
    let message = {
      message: 'Do you want to delete this participant?'
    }
    this.confirmDialogRef = this.dialog.open(ConfirmDialogComponent, {
      panelClass: 'confirm',
      disableClose: false,
      data: message
    });
    // this.confirmDialogRef.componentInstance.confirmMessage = 'Do you want to delete this participant?';
    this.confirmDialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.dataService.delete(participantId, this.configuration.participant + '/').subscribe(
          (data: any) => {
            this.toastr.success('Participant Deleted Successfully.');
            this.getParticipantList();
          }, err => {
          }
        );
      }
    });
  }

  getUserList() {
    this.dataService.getAll(this.configuration.user + '?sortBy=createdDate&orderBy=desc').subscribe((data: any) => {
      let userList = data.data;
      this.totalRecords = data.totalRecords;
      this.userList = this.userListTableData(userList);
      this.getFilteredValue();
    }, err => {
    });
  }

  getFilteredValue() {
    if (this.userList.length > 0) {
      this.filteredOptions = this.myControl.valueChanges.pipe(
        startWith(''),
        map(data => this._filter(data))
      );
    }
  }

  private _filter(value: string): any[] {
    let customerData = JSON.stringify(this.userList);
    let customerList = JSON.parse(customerData);
    const filterValue = value.toLowerCase();
    return customerList.filter(option => option.fullName
      .toString()
      .toLowerCase()
      .includes(filterValue)
    );
  }

  onSelectedUser(event) {
    this.selectedUserId = event.option.id.id;
    this.conductedByUserImage = event.option.id.profileImageURL;

    this.isProfileImage = true;
  }

  async searchUser() {
    if (this.myControl.value != undefined && this.myControl.value != "") {
      this.userList = [];
      await this.dataService.getAll(this.configuration.user + '?firstName=' + this.myControl.value).subscribe((data: any) => {
        let userList = data.data;
        this.userList = this.userListTableData(userList);
        this.getFilteredValue();
      }, err => {
      });
    }
    else {
      this.getUserList();
    }
  }

  userListTableData(userData) {
    let userListData = [];
    userData.forEach((element, index) => {
      userListData.push({
        id: element._id,
        role: element.role.displayName,
        firstname: element.firstName,
        lastname: element.lastName,
        fullName: element.firstName + ' ' + element.lastName,
        email: element.email,
        profileImageURL: element.profileImageURL,
        isActive: element.isActive == true
      });
    });

    return userListData;
  }

  async getRoles() {
    await this.dataService.getAll(this.configuration.roles).subscribe(
      (data: any) => {
        this.roles = data.data.data;
      })
  }

  selectedRole(role) {
    this.workshopForm.controls['roleName'].setValue(role);
    let roleExists = this.roles.find(roleItems => roleItems.name == role);
    if (roleExists) {
      this.selectedRoleId = roleExists._id;
      this.selectedRoleName = roleExists.name;
    }
  }

  keyPress(event: any) {
    const pattern = /[0-9\+\ ]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (event.keyCode != 8 && !pattern.test(inputChar)) {
      event.preventDefault();
    }
  }

  deleteWorkshop() {
    let message = {
      message: 'Do you want to delete this workshop?'
    }
    this.confirmDialogRef = this._matDialog.open(ConfirmDialogComponent, {
      panelClass: 'confirm',
      disableClose: false,
      data: message
    });
    // this.confirmDialogRef.componentInstance.confirmMessage = 'Do you want to delete this workshop?';
    this.confirmDialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.dataService.delete(this.workshopId, this.configuration.workshop + '/').subscribe(
          (data: any) => {
            this.toastr.success('Workshop deleted successfully!!');
            this.router.navigate(['/map/workshop']);
          }, err => {
          }
        );
      }
    });
  }

}
